import path from 'path';
import axios from '@nextcloud/axios';
import { getAppRootUrl, generateOcsUrl } from '@nextcloud/router';
const APP_URL = getAppRootUrl('user_ldap');
const AJAX_ENDPOINT = path.join(APP_URL, '/ajax');
const OCS_APP_URL = generateOcsUrl('apps/user_ldap');
const CONFIG_API_ENDPOINT = path.join(OCS_APP_URL, '/api/v1/config');
console.log(OCS_APP_URL);
console.log(CONFIG_API_ENDPOINT);
/**
 *
 * @param config
 */
export async function createConfig(config) {
  const response = await axios.post(CONFIG_API_ENDPOINT, config);
  return {
    configId: response.data.configId,
    config: response.data.config
  };
}
/**
 *
 * @param configId
 * @param config
 */
export async function updateConfig(configId, config) {
  const response = await axios.put(path.join(CONFIG_API_ENDPOINT, configId), config);
  return response.data;
}
/**
 *
 * @param configId
 */
export async function deleteConfig(configId) {
  await axios.delete(path.join(CONFIG_API_ENDPOINT, configId));
  return true;
}
/**
 * Starts a configuration test.
 * @param configId
 */
export async function testConfiguration(configId) {
  const response = await axios.post(path.join(AJAX_ENDPOINT, 'testConfiguration.php'), undefined, {
    params: {
      ldap_serverconfig_chooser: configId
    }
  });
  return response.data; // TODO: check response content
}
/**
 *
 * @param subject
 */
export async function clearMapping(subject) {
  const response = await axios.post(path.join(AJAX_ENDPOINT, 'clearMappings.php'), undefined, {
    data: {
      ldap_clear_mapping: subject
    }
  });
  return response.data; // TODO: check response content
}
/**
 * Calls the wizard endpoint.
 * @param action
 * @param configId
 */
export async function callWizard(action, configId) {
  const params = new FormData();
  params.set('action', action);
  params.set('ldap_serverconfig_chooser', configId);
  const response = await axios.post(path.join(AJAX_ENDPOINT, 'wizard.php'), params);
  return response.data;
}